@import url('https://fonts.googleapis.com/css2?family=Onest:wght@100..900&display=swap');

:root {
  --border-mode: 1px;
  --border-radius-20: 20px;

  --general-ff: 'Onest';
  --general-contrast-color: #cdf70e;
  --general-bg-color: #1f1f1f;
  --general-font-color: #ffffffcc;
  --general-border-color: #616161;
  --one-width: 20rem;
  --one-height: 28rem;
  --one-weight-text: 400;
  --general-skill-icon-color: #818181;

  /* fonts sizes */
  --font-size-base-1: 1.2rem;
  --font-size-base-2: 1.3rem;
  --font-size-base-3: 1.5rem;
  --font-size-base-4: 2rem;
  --font-size-base-5: 2.5rem;
  --font-size-base-6: 4rem;
}

html {
  scroll-behavior: smooth;
  font-family: var(--general-ff);
  background-color: var(--general-bg-color);
  color: var(--general-font-color);
  border: var(--border-mode) solid red;
  display: flex;
  justify-content: center;
}

body {
  border: var(--border-mode) solid blue;
  width: 90%;
}

a {
  text-decoration: none;
  color: var(--general-font-color);
}

.logo {
  display: flex;
  user-select: none;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  width: var(--one-width);
  transition: 0.3s;
  border: var(--border-mode) solid red;
}

.logo:hover {
  transform: scale(1.1);
}

.logo:active {
  transform: scale(1.2);
}

.header {
  z-index: 10;
  display: flex;
  position: sticky;
  top: 0rem;
  flex-direction: row;
  justify-content: space-between;
  height: 6rem;
  margin-bottom: 1rem;
  border: var(--border-mode) solid red;
}

.header-btns {
  transition: 0.3s;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.burger-menu {
  transition: 0.3s;
  display: none;
}

.ancor {
  cursor: pointer;
  user-select: none;
  border-bottom: 1px solid var(--general-contrast-color);
  margin: 1rem 0rem 0rem 4rem;
  padding: 1rem 0rem 0rem 0rem;
  transition: 0.3s;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.ancor:hover {
  border-bottom: 1px solid var(--general-skill-icon-color);
}

/* Unit */
.unit {
  font-size: var(--font-size-base-1);
  z-index: 11;
  border-radius: var(--border-radius-20);
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid var(--general-border-color);
  width: var(--one-width);
  height: var(--one-height);
  position: sticky;
  top: 0rem;
  left: 0; /* Устанавливаем начальное значение для left */
  transition: 0.3s; /* Плавный переход для свойства left */
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.avatar {
  margin: 1rem;
  transition: 0.3s;
  filter: grayscale(100%);
}

.avatar:hover {
  filter: grayscale(0%);
}

.avatar * {
  border-radius: var(--border-radius-20);
  width: 100%;
}

.contacts {
  margin: 0.5rem 0rem;
  display: flex;
  flex-direction: column;
}

.contacts * {
  /* margin: 0.5rem 0rem; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  border-radius: var(--border-radius-20);
  margin: 0.5rem 1rem;
  height: 3rem;
  cursor: pointer;
  background-color: var(--general-contrast-color);
  justify-content: center;
  transition: 0.3s;
}

.btn:hover {
  background-color: var(--general-skill-icon-color);
}

.border {
  border-radius: var(--border-radius-20);
  font-size: var(--font-size-base-1);
  height: 80%;
  width: 97%;
  justify-content: center;
  border: 1px solid var(--general-bg-color);
  color: var(--general-bg-color);
  flex-direction: row;
}

.tg-icon {
  width: 10%;
}

/* Body */
.face-page {
  border: var(--border-mode) solid rgb(0, 255, 251);
  display: flex;
}

.about-me {
  border: var(--border-mode) solid rgb(221, 255, 0);
  margin: 0% 5%;
  flex: 1;
}

.contrast-text {
  color: var(--general-contrast-color);
}

/* Blocks */

.block {
  padding: 7rem 0rem 0rem 0rem;
  border: var(--border-mode) solid rgb(255, 0, 230);
}

.block-contacts {
  display: flex;
}

.block-footer {
  height: var(--one-height);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: var(--border-mode) solid rgb(255, 0, 230);
}
.block-footer img {
  height: 40%;
}

.hello {
  font-weight: var(--one-weight-text);
  border: var(--border-mode) solid rgb(217, 0, 255);
  display: flex;
  flex-direction: column;
  height: var(--one-height);
  justify-content: center;
}

.tegs {
  display: flex;
  flex-wrap: wrap;
}

.teg {
  border-radius: 6px;
  margin: 0rem 1rem 1rem 0rem;
  padding: 0.3rem 0.7rem;
  background-color: var(--general-skill-icon-color);
  transition: 0.1s;
  user-select: none;
  cursor: pointer;
}

.teg-text {
  color: var(--general-bg-color);
  font-size: var(--font-size-base-3);
  font-weight: 600;
}

.contact-teg:hover,
.work-teg:hover,
.service-teg:hover,
.skill-icon:hover,
.teg:hover {
  transform: scale(1.05);
  color: var(--general-contrast-color);
}

.contact-teg:active,
.work-teg:active,
.service-teg:active,
.skill-icon:active,
.teg:active {
  transform: scale(1.1);
}

.hello-text {
  font-size: var(--font-size-base-6);
  margin: 1rem 0rem;
}

.block-header {
  font-size: var(--font-size-base-5);
  margin: 2rem 0rem;
}

.skills-icons {
  width: 60vw;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
  overflow-x: auto;
}

.skill-icon {
  transition: 0.1s;
  color: var(--general-skill-icon-color);
  margin: 0rem 2rem 2rem 0rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: var(--border-mode) solid rgb(0, 255, 0); */
}

.skill-icon img {
  height: 4rem;
  margin: 0rem 0rem 0.5rem 0rem;
}

.service-teg {
  user-select: none;
  cursor: pointer;
  font-size: var(--font-size-base-4);
  font-weight: 100;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 2rem;
  margin: 0rem 10% 1rem 10%;
  justify-content: space-between;
  flex-direction: row;
  border: 1px solid var(--general-border-color);
  transition: 0.1s;
}

.work-teg {
  margin: 0rem 10% 1rem 10%;
  user-select: none;
  cursor: pointer;
  font-size: var(--font-size-base-3);
  font-weight: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  padding: 1rem;
  flex-direction: row;
  border: 1px solid var(--general-contrast-color);

  transition: 0.1s;
}

.work-teg img {
  height: 2rem;
}

.contact-teg {
  display: flex;
  margin: 0rem 1rem 0rem 0rem;
  flex-direction: row;
  font-size: var(--font-size-base-2);
  border: var(--border-mode) solid rgb(217, 255, 0);
  align-items: center;
  transition: 0.1s;
}

.contact-info {
  margin: 0rem 1rem;
}
