/* Общие стили для всех устройств */
* {
  box-sizing: border-box;
}

/* Небольшие телефоны: iPhone SE, Galaxy A51, портретная ориентация */
@media (max-width: 820px) {
  :root {
    --border-radius-20: 10px;

    --one-width: 10rem;
    --one-height: 20rem;
    --one-weight-text: 400;

    /* fonts sizes */
    --font-size-base-1: 0.7rem;
    --font-size-base-2: 1.1rem;
    --font-size-base-3: 1rem;
    --font-size-base-4: 1.3rem;
    --font-size-base-5: 1.4rem;
    --font-size-base-6: 2rem;
  }

  .header {
    z-index: 10;
    display: flex;
    position: sticky;
    top: 0rem;
    flex-direction: row;
    justify-content: space-between;
    height: 3rem;
    margin-bottom: 1rem;
    border: var(--border-mode) solid red;
  }

  .ancor {
    margin: 1rem 0rem 0rem 3rem;
  }

  .unit {
    width: var(--one-width);
    height: auto;
    position: fixed;
    top: 4rem;
    left: calc(-0.89 * var(--one-width));
  }

  .unit.swiped {
    transform: translateX(var(--one-width));
  }

  .avatar {
    margin: 0.5rem;
  }

  .btn {
    height: 2rem;
  }

  .header-btns {
    display: none;
    flex-direction: column;
    align-items: normal;
  }

  .header-btns.active {
    display: flex;
  }

  .burger-menu {
    display: block;
  }

  .burger-menu.active {
    display: none;
  }

  .about-me {
    margin: 0% 0%;
  }

  .hello-text {
    font-size: var(--font-size-base-6);
    margin: 1rem 0rem;
  }
  .tegs {
    display: flex;
    flex-wrap: wrap;
  }

  .teg {
    border-radius: 6px;
    margin: 0rem 0.5rem 0.5rem 0rem;
    padding: 0.3rem 0.7rem;
    background-color: var(--general-skill-icon-color);
    transition: 0.1s;
    user-select: none;
    cursor: pointer;
  }

  .teg-text {
    color: var(--general-bg-color);
    font-size: var(--font-size-base-3);
    font-weight: 600;
  }

  .block {
    padding: 3rem 0rem 0rem 0rem;
    border: var(--border-mode) solid rgb(255, 0, 230);
  }

  .block-header {
    font-size: var(--font-size-base-5);
    margin: 1rem 0rem;
  }

  .skills-icons {
    width: 85vw;
    gap: 0.5rem;
  }

  .skill-icon {
    margin: 0rem 1rem 1rem 0rem;
  }

  .skill-icon img {
    height: 3rem;
  }

  .service-teg {
    padding: 1rem;
    margin: 0rem 0% 1rem 0%;
  }

  .work-teg {
    padding: 0.5rem;
    margin: 0rem 0% 1rem 0%;
  }

  .contact-teg {
    margin: 0rem 0.5rem 0rem 0rem;
  }

  .contact-info {
    margin: 0rem 0.5rem;
  }
}

